<template>
  <div class="home">
    <layout :active="1">
        <transition-group tag="ul" name="indexlist">
          <li class="homeimg" v-for="(list,index) in slideList" :key="index" v-show="index===currentIndex">
            <img  :src="list.image" :alt="list.desc">
          </li>
        </transition-group>
    </layout>
  </div>
</template>
<script>   

  import layout from './modules/layout.vue'
  import {homedata} from './mock-data/home.js'
  export default {
    data(){
      return {
        slideList: homedata,
        currentIndex: 0,
      }
    },
    components:{
      layout,
    },
    methods:{
      go() {
        this.timer = setInterval(() => {
          this.autoPlay()
        },4000)
      },
      stop() {
        clearInterval(this.timer)
        this.timer = null
      },
      change(index) {
        this.currentIndex = index
      },
      autoPlay() {
        this.currentIndex++
        if (this.currentIndex > this.slideList.length - 1) {
          this.currentIndex = 0
        }
      }
    },
    created(){
      this.timer = setInterval(() => {
        this.autoPlay()
      },4000)
    }
  }
</script>

<style lang="less" scoped>
  .indexlist-enter-active,.indexlist-leave-active {
    transition: all 1s ease;
    opacity:1;
  }

  .indexlist-enter,.indexlist-leave {
    opacity:0;
  }
  .hide{
    opacity: 0;
  }
  .home .homeimg{
    position: absolute;
    height: 332px;
    top:-47px;
    bottom: 0;
    margin:auto 0;
    img{
      height: 100%;
    }
  }
  @media screen and (max-width: 768px) { 
      .home .homeimg{
        margin-top:50px;
        position: static;
        height:auto;
      }


  }
</style>



